<template>
  <cube-page
    :isBack="false"
    class="page-questionnaire"
    title=""
  > 
    <demo-questionnaire
      slot="content"
      v-if="showQuest"
      :tip="tip"
      :questions="questions"
      :submit="submit"
      @submit="submitHandler"
      @validate="validateHandler"
      :modelData="modelData"
    />
  </cube-page>
</template>

<script type="text/ecmascript-6">
import CubePage from '../../components/common-components/page.vue'
import DemoQuestionnaire from '../../components/questionnaire/questionnaire.vue'
import wx from 'weixin-js-sdk';
import compress from '../../modules/image'
export default {
  data () {
    return {
      showQuest: false,
      modelData: {},
      tip: '</br><span></span> </br> / </br> <span></span> </br> <span></span> </br> <span></span><span> !</span></br></br></br> <span></span>',
      questions: [
        {
          type: 'input',
          model: 'name',
          title: '',
          options: {
            placeholder: ''
          },
          required: true
        },
        {
          type: 'radio',
          model: 'sex',
          title: '',
          options: [
            { label: '', value: 'boy' },
            { label: '', value: 'girl' }
          ],
          required: true
        },
        {
          type: 'input',
          model: 'age',
          title: '',
          options: {
            placeholder: '',
            type: 'number'
          },
          required: true
        },
        {
          type: 'input',
          model: 'school',
          title: '',
          options: {
            placeholder: ''
          },
          required: true
        },
        {
          type: 'input',
          model: 'major',
          title: '',
          options: {
            placeholder: ''
          }
        },
        {
          type: 'input',
          model: 'selfInfo',
          title: '',
          options: {
            placeholder: '',
            type: 'textarea'
          },
          required: true
        },
        {
          type: 'input',
          model: 'orterInfo',
          title: '',
          options: {
            placeholder: ''
          }
        },
        {
          type: 'radio',
          model: 'contactType',
          title: '',
          options: [
            { label: '', value: '' },
            { label: 'QQ', value: 'QQ' },
            { label: '', value: '' }
          ],
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: '',
          options: {
            placeholder: ''
          },
          on: {
            model: 'contactType',
            options: ''
          },
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: 'QQ',
          options: {
            placeholder: 'QQ'
          },
          on: {
            model: 'contactType',
            options: 'QQ'
          },
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: '',
          options: {
            placeholder: ''
          },
          on: {
            model: 'contactType',
            options: ''
          },
          required: true
        },
        {
          type: 'upload',
          model: 'image',
          title: '',
          options: {
            action: { target: '/fileUpload', data: { token: 'rwwXO0wQq5SBEqP7' } },
            max: 1,
            'process-file': (file, next) => {
              compress(file, {
                compress: {
                  width: 1600,
                  height: 1600,
                  quality: 0.5
                }
              }, next)
            }
          },
          messages: '',
          events: {
            'file-removed': (...args) => {
              console.log('file removed', args)
            }
          },
          required: false
        }
      ],
      submit: {
        text: ''
      }
    }
  },
  components: {
    CubePage,
    DemoQuestionnaire
  },
  mounted () {
    this.$store.commit('SET_SESSION_ID', 'noSession')
    this.getDeorderInfo();
  },
  methods: {
    submitHandler (model) {
      console.log('submit', model)
      let queryData = { openid: this.openid };
      Object.keys(model).forEach((key) => {
        if (key === 'image') {
          if (model[key] && model[key][0]) {
            queryData[key] = model[key][0].response.url;
          }
        } else {
          queryData[key] = model[key]
        }
      })
      this.uploadInfo(queryData);
    },
    uploadInfo (info) {
      const toast = this['$createToast']({
        time: 0,
        txt: '...',
        mask: true
      })
      toast.show()
      this.$store.dispatch('submitDeorderInfo', { ...info }).then((response) => {
        if (response.success) {
          toast.hide();
          this.$createDialog({
            type: 'alert',
            title: '',
            content: '',
            icon: 'cubeic-right',
            onConfirm: () => {
              wx.closeWindow();
            }
          }).show()
        }
      })
    },
    getDeorderInfo () {
      let query = this.$route.query
      if (query.code) {
        const toast = this['$createToast']({
          time: 0,
          txt: '...'
        })
        toast.show()
        this.$store.dispatch('getDeorderInfo', { code: query.code }).then((response) => {
          if (response.success) {
            if (response.data.openid) {
              this.openid = response.data.openid
              if (response.data.name) {
                this.submit.text = '';
                this.$set(this, 'modelData', response.data);
              }
              this.showQuest = true;
            }
          } else {
            this.$createDialog({
              type: 'alert',
              title: '',
              content: '',
              onConfirm: () => {
                wx.closeWindow();
              }
            }).show()
          }
          toast.hide()
        })
      } else {
        console.log('')
      }
    },
    validateHandler (result) {
      let index = result.firstInvalidFieldIndex;
      if (index > -1) {
        let text = this.questions[index].title + '';
        const toast = this['$createToast']({
          time: 3000,
          txt: text,
          type: 'text'
        })
        toast.show()
      }
    }
  }
}
</script>
<style lang="stylus">
.cube-page
  &.page-questionnaire
    .wrapper
      .content
        margin 0
</style>
